
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-indent {
  text-indent: 2em;
}
.text-xs {
  font-size: 60%;
}
.text-sm {
  font-size: 75%;
}
.text-md {
  font-size: 100%;
}
.text-lg {
  font-size: 150%;
}
.pagination {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  margin-top: 3rem;
  font-size: 0;
  border-top: 1px solid #f8f8f8;
}
.pagination .pagination_link, .pagination .pagination_space {
    display: inline-block;
    position: relative;
    min-width: 2rem;
    height: 2rem;
    line-height: 2rem;
    font-size: 14px;
    text-align: center;
    color: #999;
    -webkit-transition: all ease 0.3s;
    transition: all ease 0.3s;
}
.pagination .pagination_link:before, .pagination .pagination_space:before {
      content: '';
      position: absolute;
      left: 0;
      top: -1px;
      width: 100%;
      height: 1px;
      border-top: 1px solid transparent;
}
.pagination_link:hover {
    color: #333;
}
.pagination_link:hover:before {
      border-color: #333;
}
.pagination_current {
    background: #f8f8f8;
}



