.catalogue {
  text-align: left;
  font-size: 16px;
  max-height: 90vh;
}
.catalogue .catalogue_group {
    margin: 0 0 0 20px;
    padding: 0;
    list-style: none;
}
.catalogue .catalogue_item {
    line-height: 30px;
    display: block;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.catalogue .catalogue_item:hover {
      color: #fff;
}



