
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-indent {
  text-indent: 2em;
}
.text-xs {
  font-size: 60%;
}
.text-sm {
  font-size: 75%;
}
.text-md {
  font-size: 100%;
}
.text-lg {
  font-size: 150%;
}
.popup {
  position: fixed;
  z-index: 99999;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  -webkit-animation: shadow-show 0.01s linear 0.5s forwards;
  animation: shadow-show 0.01s linear 0.5s forwards;
}
@-webkit-keyframes shadow-show {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@-webkit-keyframes show {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes show {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.loading_dot {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-color: #6699FF;
  border-radius: 50%;
  -webkit-animation: sk-three-bounce 1.4s ease-in-out 0s infinite both;
  animation: sk-three-bounce 1.4s ease-in-out 0s infinite both;
}
.loading_dot:nth-of-type(1) {
    -webkit-animation-delay: -0.3s;
    animation-delay: -0.3s;
}
.loading_dot:nth-of-type(2) {
    -webkit-animation-delay: -0.15s;
    animation-delay: -0.15s;
}
@-webkit-keyframes sk-three-bounce {
0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
}
40% {
    -webkit-transform: scale(1);
    transform: scale(1);
}
}
@keyframes sk-three-bounce {
0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
}
40% {
    -webkit-transform: scale(1);
    transform: scale(1);
}
}



