
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-indent {
  text-indent: 2em;
}
.text-xs {
  font-size: 60%;
}
.text-sm {
  font-size: 75%;
}
.text-md {
  font-size: 100%;
}
.text-lg {
  font-size: 150%;
}
.archives {
  font-size: 16px;
  border-left: 4px solid #f5f5f5;
}
.archives .archives_count, .archives .archives_title, .archives .archives_item {
    position: relative;
    padding-left: 20px;
}
.archives .archives_count:before, .archives .archives_title:before, .archives .archives_item:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      bottom: 0;
      margin: auto 0;
      background: #aaa;
}
.archives_count {
    position: relative;
    top: -0.5em;
}
.archives_count:before {
      width: 12px;
      height: 12px;
      border-radius: 12px;
      left: -8px;
}
.archives_title {
    font-weight: normal;
    padding-top: 40px;
    padding-bottom: 40px;
    font-size: 150%;
}
.archives_title:before {
      width: 10px;
      height: 10px;
      border-radius: 10px;
      left: -7px;
}
.archives_item {
    line-height: 3em;
    color: #666;
    border-bottom: 2px dashed #f5f5f5;
}
.archives_item:before {
      width: 6px;
      height: 6px;
      border-radius: 6px;
      left: -5px;
}
.archives_item:hover {
      color: #000;
}
.archives_item:hover:before {
        background: #333;
}
.archives_link {
    display: block;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.archives_date {
    font-size: 12px;
    margin-right: 15px;
}



