
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-indent {
  text-indent: 2em;
}
.text-xs {
  font-size: 60%;
}
.text-sm {
  font-size: 75%;
}
.text-md {
  font-size: 100%;
}
.text-lg {
  font-size: 150%;
}
.article_tt {
  position: relative;
}
.article_tt:after {
    content: '';
    position: absolute;
    left: 0;
    bottom: -3px;
    height: 2px;
    width: 100%;
    background: currentcolor;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transition: all ease .2s;
    transition: all ease .2s;
}
.article_tt:hover:after {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
@media screen and (min-width: 920px) {
.article {
    padding: 1.33333rem;
    background-color: #fff;
    border: 1px solid #ddd;
    z-index: 100;
    box-shadow: 7px 6px 16px #888;
    -webkit-transition: all ease .3s;
    transition: all ease .3s;
}
.article:hover {
      box-shadow: 14px 11px 27px #888;
}
}
.article {
  margin-bottom: 3rem;
  font-size: 16px;
  color: #333;
}
.article_hd {
    font-weight: normal;
}
.article_info {
    font-size: 75%;
    color: #999;
    word-spacing: 3px;
}
.article_ft {
    margin: 1rem 0;
}
.article_nav {
    zoom: 1;
}
.article_nav:after {
      content: "";
      display: block;
      clear: both;
}
.article_prev {
    float: left;
}
.article_next {
    float: right;
}
.article_ct {
    margin: 10px 0;
    line-height: 2em;
}
.article_ct a {
      color: #6699FF;
      text-decoration: underline;
}
.article_ct a:hover {
        color: #0055ff;
}
.article_ct img {
      max-width: 100%;
}
.article_ct table {
      width: 100%;
      border-collapse: collapse;
}
.article_ct th, .article_ct td {
      border: 1px solid #d6d6d6;
}



