
@charset "UTF-8";
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-indent {
  text-indent: 2em;
}
.text-xs {
  font-size: 60%;
}
.text-sm {
  font-size: 75%;
}
.text-md {
  font-size: 100%;
}
.text-lg {
  font-size: 150%;
}
.main {
  position: relative;
  /* 这里主要是为了让所有页面都保持滚动条，防止页面抖动*/
  min-height: 101vh;
  -webkit-transition: all ease 0.3s;
  transition: all ease 0.3s;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
}
.main > .container {
    -webkit-box-flex: 2;
    -ms-flex-positive: 2;
    flex-grow: 2;
}
.main.active {
    padding-right: 300px;
}
.page {
  min-height: calc(100vh - 78px - 98px);
  padding-top: 2.66667rem;
  padding-bottom: 2.66667rem;
}



