.tab {
  font-size: 16px;
}
.tab_nav {
    height: 40px;
    margin: 0;
    padding: 0;
    list-style: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.tab_item {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
    line-height: 40px;
    margin: 0 10px;
    color: #999;
    text-align: center;
    cursor: pointer;
    border-bottom: 1px solid #d6d6d6;
}
.tab_item.active {
      color: #6699FF;
}
.tab_panel {
    display: none;
    width: 100%;
    padding-top: 30px;
}
.tab_panel.active {
      display: block;
}



